/* navbar.module.css */
:root {
  --scrolled: rgba(255, 255, 255, 1);
}

.bodyPadding {
  height: 70px;
  width: 100%;
}

.title {
  height: 70px;
}

.brand {
  padding: 12px 10px;
  font-size: 1.2rem;
  height: 70px;
}

.x {
  width: 100%;
  color: #004d40; /* Deep peacock blue */
}

.navbar {
  color: #004d40; /* Deep peacock blue */
  position: fixed;
  top: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  transition: 1s;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.9); /* Transparent white background */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  list-style-type: none;
  float: left;
}

a {
  height: 70px;
}

li a {
  display: block;
  color: inherit;
  text-align: center;
  padding: 20px 16px;
  text-decoration: none;
  font-weight: 600;
}

li a:hover {
  text-decoration: none;
  color: inherit;
}
